// eslint-disable-next-line
const Webhook_Server = "https://api.${region}.500apps.com/500conference/webhook";
import axios from "axios";
import Data from "../core/data";

class Webhook extends Data {
  constructor() {
    super(Webhook_Server);
    this.disablePCors();
  }
  async sendDataToWebhook(form, env) {
    let results;
    try {
      // eslint-disable-next-line no-unused-vars
      let region = env;
      this.url = eval("`" + `${Webhook_Server}` + "`");
      results = await axios.post(this.getPCors(), form);
    } catch (err) {
      results = false;
    }
    return results;
  }
}

export default new Webhook();
