import Data from "@/js/core/data";
import ENV from "@/js/core/env.js";

const User_Server = ENV.get_usersurl() + "/auth/v1/users/${id}?fields=name,email,pic_path";
class Users extends Data {
  constructor() {
    super(User_Server);
    this.disablePCors();
  }
  async get(id, env) {
    // Override the super class method to set headers.
    this.getConfig = () => {
      return {
        headers: {
          Authorization: `Bearer ${window.JWT}`,
          withCredentials: true,
        },
      };
    };
    // eslint-disable-next-line no-unused-vars
    let region = env;
    this.url = eval("`" + `${User_Server}` + "`");
    // Get user info based on id for avatar & name.
    let response = await super.get();
    return response[0];
  }
}
export default new Users();
