const USERS_ENV_URLS = [
  { name: "dev", url: "https://api.dev.500apps.com" },
  { name: "qa", url: "https://api.qa.500apps.com" },
  { name: "prod", url: "https://api.500apps.com" },
];

class Env {
  constructor() {
    if (location.hostname === "localhost" || location.hostname === "127.0.0.1" || window.location.href.includes(":808")) this.env = "dev";
    else {
      const region = window.region;
      if (region == "dev") this.env = "dev";
      else if (region == "qa") this.env = "qa";
      else this.env = "prod";
    }
  }

  get_userenv() {
    return this.env;
  }

  get_usersurl() {
    let envurl = USERS_ENV_URLS.filter((users_env_url) => users_env_url.name === this.env);
    return envurl[0].url;
  }
}

export default new Env();
