import Data from "@/js/core/data";

const Shortcut_Server = "https://api.500apps.com/shortcuts/shortcut";
const Room_Server = "https://api.${region}.500apps.com/500conference";

class Shortcut extends Data {
  constructor() {
    super(Shortcut_Server);
    super.disablePCors();
  }
  async get(roomname, enable) {
    this.url = `${Shortcut_Server}/${roomname}`;
    let response = await super.get();
    if (enable && response.length) {
      // eslint-disable-next-line no-unused-vars
      let region = response[0].env;
      this.url = eval("`" + ` ${Room_Server}/${enable}/${response[0].id}` + "`");
      response = await super.get();
      if (response.length) response[0].env = region;
    }
    return response;
  }
}

export default new Shortcut();
