// eslint-disable-next-line
const Cookie_Server = "https://api.${region}.500apps.com/500conference/authorize";
import axios from "axios";
import Data from "../core/data";

class Cookie extends Data {
  constructor() {
    super(Cookie_Server);
    this.disablePCors();
  }
  async getCookieForRoom(form, env) {
    let results;
    try {
      // eslint-disable-next-line no-unused-vars
      let region = env;
      this.url = eval("`" + `${Cookie_Server}` + "`");
      results = await axios.post(this.getPCors(), form);
    } catch (err) {
      results = false;
    }
    return results;
  }
}

export default new Cookie();
