<template>
  <div style="height:100vh">
    <div class="d-flex justify-content-center" v-if="loading && !enableError">
      <b-row class="vh-100">
        <b-col md="12" class="m-auto">
          <div class="text-center">
            <b-spinner class="my-4" variant="primary" label="Spinning"></b-spinner>
          </div>
          <div class="text-center">
            <h2>Powered by 500apps</h2>
          </div>
        </b-col>
      </b-row>
    </div>
    <VueJitsiMeet ref="jitsiRef" domain="qa.conference.ly" :options="jitsiOptions" v-if="enable"></VueJitsiMeet>
    <iframe id="loggedinId" style="display: none;" src="https://infinity.500apps.com/ls.html"></iframe>
    <b-container v-if="enableError">
      <b-row class="vh-100">
        <b-col md="6" class="m-auto">
          <b-card class="mb-0" body-class="p-5">
            <div class="text-center mb-4">
              <b-img src="/img/failure-img.gif" alt="logo" height="70" />
            </div>
            <div class="text-center">
              <h2 class="mb-4">{{ organization ? "You can't enter this room" : "Invalid room name" }}</h2>
              <b-button variant="primary" class="px-4" @click="redirect">Return to 500conference</b-button>
            </div>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  import { JitsiMeet } from "@mycure/vue-jitsi-meet";
  import { admin, guest } from "@/js/conference/config";
  import InterfaceConfigJs from "@/js/conference/interface-config.js";
  import Shortcut from "../../js/conference/shortcut";
  import UserJs from "../../js/conference/user";
  import Webhook from "@/js/conference/webhook.js";
  import CookieJs from "@/js/conference/get-cookie.js";
  import ValidateOTP from "@/js/conference/validate.js";
  export default {
    components: {
      VueJitsiMeet: JitsiMeet,
    },
    data() {
      return {
        loading: true,
        roomName: "",
        enable: false,
        config: {},
        is_admin: false,
        roomInfo: {},
        userInfo: {},
        jwt: "",
        subdomain: "",
        enableError: false,
        organization: false,
      };
    },
    async mounted() {
      if (window.location.host.includes(".conference.ly")) this.subdomain = window.location.host.split(".conference.ly")[0];
      else this.subdomain = window.location.host.split(".500conference.com")[0];
      document.getElementById("loggedinId").onload = this.loadIframe;
      // Added listener to get jwt.
      let that = this;
      // If host includes .conference.ly
      if (window.location.host.includes(".conference.ly") || window.location.host.includes(".500conference.com")) {
        // Add listener
        window.addEventListener("message", async function(event) {
          if (event.data.message == "verify_user") {
            if (that.roomInfo.visibility != "Private") {
              let frame = document.getElementById("jitsiConferenceFrame0");
              frame.contentWindow.postMessage({ message: "load_jitsi_meet", response: 200 }, "*");
              return;
            }
            that.verifyUser(event.data.details);
          }
          // If action is jwt_received assign jwt to window.
          if (event.data.action == "jwt_received") {
            window.JWT = event.data.jwt;
            // Assign the random room name.
            that.roomName = that.$route.params.key ? `${that.subdomain}/${that.$route.params.key}` : `subdomain/${that.subdomain}`;
            // Get domain_id based on roomname(sub domain)
            let response;
            if (that.$route.params.key) response = await Shortcut.get(that.subdomain, that.$route.params.key);
            else response = await Shortcut.get(that.subdomain);
            that.roomInfo = response[0];
            if (!response.length) {
              that.enableError = true;
              return;
            }
            // Decode jwt to get user_id, domain_id and env.
            if (window.JWT)
              if (
                ((that.roomInfo.visibility == "Organization" || that.roomInfo.visibility == "Private") && that.roomInfo.domain_id == that.decodeJwt(event.data.jwt).tenant_id) ||
                (that.roomInfo.visibility != "Organization" && that.roomInfo.visibility != "Private")
              ) {
                let value = { jwt: true, user_details: that.decodeJwt(event.data.jwt), visibility: "jwt" };
                that.loadComponent(value);
                return "";
              }
            // Without Jwt
            let value = { jwt: false, user_details: {}, visibility: that.roomInfo.visibility ? that.roomInfo.visibility : "Public" };
            that.loadComponent(value);
          }
        });
      } else {
        // If it is public room.
        this.roomName = this.$route.params.roomname;
        this.enable = true;
        this.loading = false;
        this.config = guest;
        const that = this;
        this.$nextTick(() => {
          window.addEventListener("message", async function(event) {
            if (event.data.message == "verify_user") {
              if (that.roomInfo.visibility != "Private") {
                let frame = document.getElementById("jitsiConferenceFrame0");
                frame.contentWindow.postMessage({ message: "load_jitsi_meet", response: 200, audio: true }, "*");
                return;
              }
              that.verifyUser(event.data.details);
            }
          });
        });
      }
    },
    computed: {
      jitsiOptions() {
        return {
          roomName: this.roomName,
          height: window.innerHeight,
          parentNode: "",
          configOverwrite: this.config,
          interfaceConfigOverwrite: InterfaceConfigJs,
          onload: this.onIFrameLoad,
          jwt: this.jwt,
        };
      },
    },
    methods: {
      loadComponent(value) {
        if (value.jwt && value.user_details.user_id == this.roomInfo.created_by) {
          this.config = admin;
          this.is_admin = true;
        } else this.config = guest;
        let visibility = value.visibility;
        switch (visibility) {
          case "jwt":
            this.setAdminInfo(this.is_admin, value.user_details.user_id);
            break;
          case "Public":
          case "Protected":
          case "Private":
            this.loading = false;
            this.enable = true;
            setTimeout(() => {
              let frame = document.getElementById("jitsiConferenceFrame0");
              frame.contentWindow.postMessage({ message: "navigate_jitsi", visibility: visibility }, "*");
            }, 1500);
            break;
          case "Organization":
            // For organization, we are enabling card.(If user doesn't have jwt)
            this.enableError = true;
            this.organization = true;
            break;
        }
      },
      async verifyUser(info) {
        switch (this.roomInfo.visibility) {
          case "Public":
          case "Organization":
          case "Protected": {
            let frame = document.getElementById("jitsiConferenceFrame0");
            frame.contentWindow.postMessage({ message: "load_jitsi_meet", response: 200, audio: info.audio }, "*");
            return;
          }
          case "Private": {
            if (window.JWT && this.roomInfo.visibility == "Private" && this.roomInfo.domain_id == this.decodeJwt(window.JWT).tenant_id) {
              let frame = document.getElementById("jitsiConferenceFrame0");
              frame.contentWindow.postMessage({ message: "load_jitsi_meet", response: 200, audio: info.audio }, "*");
              return "";
            }
          }
        }
        let response = await ValidateOTP.verify(
          {
            domain_id: this.roomInfo.domain_id,
            email: info.email,
            otp: info.verificationCode,
            visibility: (this.roomInfo.visibility + "").toLowerCase(),
            room_name: (this.roomInfo.name || "").toLowerCase(),
            link: window.location.href,
            env: this.roomInfo.env,
          },
          true,
        );
        if (response.data && response.data.verification) {
          let frame = document.getElementById("jitsiConferenceFrame0");
          frame.contentWindow.postMessage({ message: "load_jitsi_meet", response: 200, audio: info.audio }, "*");
        } else {
          alert("Please verify the details you have entered.");
        }
      },
      // Called once it receive domain_id.
      loadIframe() {
        let element = document.getElementById("loggedinId");
        element.contentWindow.postMessage({ action: "get_jwt" }, "*");
      },
      // Get image, name and set to jitsi instance.
      async setAdminInfo(is_admin = false, id) {
        // eslint-disable-next-line no-unused-vars
        if (id) this.userInfo = await UserJs.get(id, this.roomInfo.env);
        this.jwt = await this.getJwt(this.userInfo, is_admin);
        let token = this.decodeJwt(this.jwt);
        let date = new Date(token.exp * 1000);
        document.cookie =
          `ui-${this.subdomain}${this.$route.params.key ? "-" + this.$route.params.key : ""}=` + (this.jwt || "") + "; expires= " + date.toUTCString() + "; path=" + window.location.pathname;
        this.loading = false;
        this.enable = true;
      },
      // Iframe onload.
      onIFrameLoad() {
        let that = this;
        this.$refs.jitsiRef.jitsiApi.addListener("participantJoined", (value) => {
          if (that.is_admin)
            Webhook.sendDataToWebhook(
              { domain_id: that.roomInfo.domain_id, created_by: that.roomInfo.created_by, room: that.roomName, displayName: value.displayName, id: value.id },
              that.roomInfo.env,
            );
        });
      },
      // Decode Jwt to get region
      decodeJwt(token) {
        let base64Url = token.split(".")[1];
        let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
        let jsonPayload = decodeURIComponent(
          atob(base64)
            .split("")
            .map(function(c) {
              return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
            })
            .join(""),
        );
        return JSON.parse(jsonPayload);
      },
      // Get Jwt based on given details.
      async getJwt(userInfo = {}, is_admin) {
        let response = await CookieJs.getCookieForRoom(
          {
            data: {
              room: this.$route.params.key ? this.$route.params.key : this.subdomain,
              avatar: userInfo.pic_path || "",
              moderator: is_admin,
              sub: "qa.conference.ly",
              name: userInfo.name || "",
              aud: "jitsi",
              email: userInfo.email || "",
              group: this.$route.params.key ? this.subdomain : "subdomain",
            },
          },
          this.roomInfo.env,
        );
        return response.data[0].token;
      },
      // Redirect to infinity.
      redirect() {
        window.open(`https://infinity.500apps.com/conference`);
      },
    },
  };
</script>
